import { createWebHistory, createRouter } from "vue-router";
import ToDoList from "@/views/Task";

const routes = [

];


const addRoute = (path, name, component) => { routes.push({path: path,name: name,component: component})};

addRoute("/","Home", ToDoList);
addRoute("","Home", ToDoList);

const router = createRouter({
    history: createWebHistory(''),
    routes,
});

export default router;