<template>
  <div class="task_todo">
    <div class="task_todo__form">
      <input type="text" ref="taskName" placeholder="Название задачи">
      <input type="date" ref="taskDate" name="" id="">
      <button @click="addTodo">Добавить</button>
    </div>
    <div class="todo-task-list">
      <div class="task" v-for="task in tasks" :ref="'task_' + task.todoName"  :key="task.todoName">
        <span class="task-name">Название: <b>{{task.todoName}}</b></span>
        <span class="task-date">Выполнить до: <b>{{task.todoDate}}</b></span>
        <button class="remove-btn" @click="removeTodo(task)">Удалить</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ToDo",
  data(){
    return {
      tasks : [

      ]
    }
  },
  methods : {
    save(){
      localStorage.setItem("tasks",JSON.stringify(this.tasks));
    },
    addTodo(){
      let name = this.$refs.taskName.value;
      let ts = this.$refs.taskDate.value;
      if(ts === "" || name === ""){
        alert("Вы не заполнили все поля");
        return
      }
      let day = (new Date(ts).getDate() < 10) ?`0${new Date(ts).getDate()}` : new Date(ts).getDate();
      let month = (new Date(ts).getMonth() + 1 < 10) ?`0${new Date(ts).getMonth() + 1}` : new Date(ts).getMonth() + 1;
      let year = new Date(ts).getFullYear();

      this.tasks.push({todoName: name, todoDate: `${day}.${month}.${year}`});
      this.save();
    },
    removeTodo(task){
      this.tasks.splice(this.tasks.indexOf(task),1);
      this.save();
    }
  },
  mounted() {
    if(localStorage.getItem("tasks") != null){
      this.tasks = JSON.parse(localStorage.getItem("tasks"));

    }
    else{
      this.tasks = [];
    }
  }
}
</script>

<style scoped>
.task_todo__form{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.task_todo__form * {
  margin: 1vh 0;
}
</style>